.main-container {
  border-radius: 8px;
  width: calc(100% - 354px);
  height: calc(100vh - 92px);
  min-width: 650px;
  color: white;
  margin: 8px 8px 0 0;
  min-height: 500px;
  background: linear-gradient(
    360deg,
    rgba(18, 18, 18, 1) 70%,
    rgba(255, 255, 255, 0.15) 100%
  );
}
